import { useEffect, useState } from "react"
import { useNavigate, useParams } from "react-router"
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import { getPostAPI, updatePostAPI } from "../../service/post.service";
import { searchCategoryAPI } from "../../service/category.service";
import { toast } from "react-toastify";

function UpdatePost() {
  let { id } = useParams()
  let [post, setPost] = useState({
    id,
    title: "",
    description: "",
    categoryId: '',
    imageFile: null
  })
  let navigate = useNavigate()

  let getData = async () => {
    let body = await getPostAPI(id)
    setPost(body)
  }

  useEffect(() => { getData() }, [id])

  let [categories, setCategories] = useState([])
  let [search, setSearch] = useState({
    "start": 0,
    "length": 20,
    "search": {
      "value": ""
    }
  })

  useEffect(() => {
    fetchData()
  }, [search])

  const fetchData = async () => {
    try {
      let body = await searchCategoryAPI(search) //lay then tra ve
      setCategories(body.data)
    } catch (e) {
      console.log(e)
    }
  }

  let handleChangeText = (e) => {
    setPost({ ...post, [e.target.name]: e.target.value })
  }

  let handleChangeFile = (e) => {
    setPost({ ...post, imageFile: e.target.files[0] })
  }

  let handleSubmit = async () => {
    let data = new FormData();
    data.append('title', post.title);
    data.append('description', post.description);
    data.append('categoryId', post.categoryId);
    data.append('imageFile', post.imageFile);
    data.append('id', post.id);

    console.log(data)
    await updatePostAPI(data)
    toast("update thanh cong")
    navigate("/posts")
  }

  return <div>
    <h1>New Post</h1>
    <div>
      <div>
        <label>Title:</label>
        <input value={post.title} name="title" onChange={handleChangeText} />
      </div>

      <div>
        <label>Description:</label>
        <ReactQuill style={{ height: 150 }} theme="snow" value={post.description}
          onChange={(text) => setPost({ ...post, description: text })} />
        {/* <input value={post.description} name="description" onChange={handleChangeText} /> */}
      </div>

      <div style={{ marginTop: 50 }} >
        <label>categoryId:</label>
        <select value={post.categoryId} name="categoryId" onChange={handleChangeText}>
          {categories.map(c => {
            return <option key={c.id} value={c.id}>{c.name}</option>
          })}
        </select>
      </div>

      <div>
        <label>Image File:</label>
        <input type="file" accept="image/*" onChange={handleChangeFile} />
      </div>
      <button onClick={handleSubmit}>Save</button>
    </div>
  </div>
}
export default UpdatePost