import { useEffect, useState } from 'react';

function TestPage() {
  let [personList, setPersonList] = useState([{ id: 1, name: "A1" }])
  let [editPerson, setEditPerson] = useState(null)

  return (
    <>
      <h1>Hello JSX</h1>
      <p>Hello JSX</p>
      {/* <Hi name="ReactJS" id="1" />
      <Hi name="Jmaster.io" id="2" src="test.js" /> */}
      <Form setPersonArray={setPersonList} editPerson={editPerson} />
      <Table setEditPerson={setEditPerson} personArray={personList} setPersonArray={setPersonList} />
    </>
  );
}

function Table({ personArray, setPersonArray, setEditPerson }) {
  // const { personArray } = prop //object destruction
  // [
  //   { id: 1, name: "A1" },
  //   { id: 2, name: "A2" },
  //   { id: 3, name: "A3" },
  //   { id: 4, name: "A4" }
  // ]
  //lap va tra ve mang moi, voi phan tu return
  // let newArr = personArray.map(p => {
  //   return (<tr>
  //     <td>{p.id}</td>
  //     <td>{p.name}</td>
  //   </tr>)
  // })
  // console.log(newArr)
  const deleteRow = (id) => {
    //xoa phan tu trong mang co p.id = id truyen vao
    let newArr = personArray.filter(p => p.id !== id)
    setPersonArray(newArr)
  }

  return (
    <table border={1} >
      <thead>
        <tr>
          <th>ID</th>
          <th>Name</th>
          <th>Actions</th>
        </tr>
      </thead>
      <tbody>
        {
          personArray.map(p => {
            return (<tr key={p.id}>
              <td>{p.id}</td>
              <td>{p.name}</td>
              <td><button onClick={() => deleteRow(p.id)}
                type='button'>Delete</button>

                <button onClick={() => setEditPerson(p)}
                  type='button'>Edit</button>

              </td>
            </tr>)      
          })
        }
      </tbody>
    </table>
  )
}
function Form({ setPersonArray, editPerson }) {
  // state - array detructive
  let [id, setId] = useState("")
  let [name, setName] = useState("")
  // 
  useEffect(() => {
    setId(editPerson?.id || "")
    setName(editPerson?.name || "")
  }, [editPerson])

  let onChangeHandler = (e) => {
    console.log(e.target.name)
    console.log(e.target.value)
    //set gia tri cho bien id
    if (e.target.name === 'id')
      setId(e.target.value)

    if (e.target.name === 'name')
      setName(e.target.value)

  }

  let submitCreateForm = () => {
    console.log("SAVE")
    let person = { id, name }
    // {id: "1", name:"A"}
    console.log(person["id"])
    console.log(person.id)
    // if (name.trim().length < 6)
    //   return;
    //SET STATE CUA APP COMPONENT
    //THEM MOI
    //add them vao mang
    //currentVar: gia tri hien tai cua bien state personList
    setPersonArray(currentVar => ([...currentVar, person]))
  }

  let submitUpdateForm = () => {
    console.log("SAVE")
    let person = { id, name }
    // {id: "1", name:"A"}
    console.log(person["id"])
    console.log(person.id)
    // if (name.trim().length < 6)
    //   return;
    //SET STATE CUA APP COMPONENT

    //UPDATE
    if (editPerson) {
      setPersonArray(currentVar => {
        //1. tim vi tri cua editperson trong mang
        const index = currentVar.findIndex(p => p.id === editPerson.id)
        //2. thay the = doi tuong person
        console.log(index)
        //thay the
        let newArray = [...currentVar]
        newArray[index] = person

        return newArray
      })
    }
  }

  // const inputError = { backgroundColor: 'red' }
  // const inputSuccess = { backgroundColor: 'green' }

  return (
    <div>
      <label>ID:  </label>
      <input name="id" onChange={onChangeHandler} value={id} />

      <label>Name:</label>
      <input
        // style={name.length < 6 ? inputError : inputSuccess}
        className={name.length < 6 ? 'input-error' : 'input-valid'}
        name="name" onChange={onChangeHandler} value={name} />

      <button type='button' onClick={submitCreateForm}>Create</button>
      <button type='button' onClick={submitUpdateForm}>Update</button>
      <div>
        {id} - {name}
      </div>
    </div>
  )
}
function Hi(prop) {//properties - js object
  console.log(prop) //{name: 'Jmaster.io', id: '2', src: 'test.js'}
  // es6: destruction 
  let { id, name: uname } = prop
  return (
    <>
      <h1>Hello ReactJs {uname} </h1>
      <h1>Hello ReactJs {id}</h1>
    </>
  )
}
/// function component
function Hello({ vu: id, name }) {//property: js object
  // let name = prop.name
  // let id = prop.vu
  // let { vu: id, name } = prop
  return (
    <div>
      <h1>Hello React Js {name} {id}</h1>
    </div>
  )
}

export default TestPage;

