import { axiosJmaster } from "../util/axiao";

export const meAPI = (username, password) => {

    var config = {
        method: 'get',
        url: '/member/me',
        headers: {
            'Authorization': 'Bearer ' + localStorage.getItem("token"),
        },
       
    };

    return axiosJmaster(config)
        .then(function (response) {
            return (response.data)
        })
}