
import { Stack } from "@mui/material";
import { useEffect, useState } from "react"
import { useDispatch, useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom"
import { toast } from "react-toastify";
import { searchThunk } from "../../redux/categorySlice";
import { store } from "../../redux/store";
import { deleteCategoryAPI, searchCategoryAPI } from "../../service/category.service";

function ListCategory() {
    let navigate = useNavigate()
const dispatch = useDispatch();
    let {
        categories,
        recordsFiltered,
        totalPage
    } = useSelector((store) => store.category);

  //  let [categories, setCategories] = useState([])
    //   let [keyword, setKeyword] =  useState("")//

    let [search, setSearch] = useState({
        "start": 0,
        "length": 5,
        "search": {
            "value": ""
        }
    })
//    let [totalPage, setTotalpage] = useState(0)

    useEffect(() => {
        fetchData()
        console.log("online")
        // unmount chuyen trang
        return () => {
            console.log("offline")
        }
    }, [search])

    const fetchData = async () => {
        // try {
        //     let body = await searchCategoryAPI(search) // lay then tả ve

        //     console.log(body.recordsFiltered)// thuoc tinh data, xem postman
        //     setCategories(body.data)
        //     //tinh tong so trang
        //     let totalPage = Math.ceil(body.recordsFiltered / search.length)
        //     setTotalpage(totalPage)
        // } catch (error) {
        //     console.log(error);
        // }

        dispatch(searchThunk(search));

    };
    
    const handleSearch = (e) => {
        //    setKeyword(e.target.value)
        //    fetchData()
        // fetchData (e.target.value)
        setSearch({
            ...search,
            "search": {
                "value": e.target.value
            }
        })
    }
    const handleNext = () => {
        setSearch({
            ...search,
            start: search.start + search.length
        })
    }
    const handlePrew = () => {
        setSearch({
            ...search,
            start: search.start - search.length
        })
    }
    const handleChangeSize = (e) => {
        const size = e.target.value
        setSearch({
            ...search,
            length: parseInt(size)
        })
    }
    const handlePage = (index) => {
        setSearch({
            ...search,
            start: index * search.length
        })
    }

    const handleDelete = async (id) => {
        try {
            await deleteCategoryAPI(id)
           
            fetchData()

            toast("Xoa thanh cong!")

        } catch (e) {

        }
    }
    return <div>
        <Link to="/category/create">Create Category</Link>
        <div>
            <input onChange={handleSearch} />
        </div>
        <table>
            <thead>
                <tr>
                    <th>ID</th>
                    <th>Name</th>
                    <th>Actions</th>
                </tr>
            </thead>
            <tbody>
                {categories.map(c => {
                    return (
                        <tr key={c.id}>
                            <td>{c.id}</td>
                            <td>{c.name}</td>
                            <td><button onClick={() => handleDelete(c.id)}>Delete</button>
                                |<button onClick={() => navigate(`/category/update/${c.id}`)} >Edit</button></td>
                        </tr>
                    )

                })}
            </tbody>
        </table>
        <Stack direction={"row"} alignItems={"center"}>
            <select onChange={handleChangeSize}>
                <option value="5">5</option>
                <option value="10">10</option>
                <option value="15">15</option>
                <option value="20">20</option>
            </select>
            <button onClick={handlePrew}>Prew</button>
            {
                Array.apply(null, { length: totalPage }).map((_, index) => {
                    return (
                        <button onClick={() => handlePage(index)}>{index}</button>
                    )
                })
            }
            <button onClick={handleNext}>Next</button>
            <p>Tong so trang : {totalPage}</p>

            {/* <Pagination count={totalPage} variant="outlined" color="primary" showFirstButton showLastButton
        onChange={(e, value) => handlePage(value - 1)}
      /> */}
        </Stack>
    </div>


}
export default ListCategory