import { useContext } from "react";
import { Link } from "react-router-dom";
import { useMe } from "../../hook/useAuth";
import { AuthContext } from "../../context/AuthContext";
import { ThemeContext } from "../../context/ThemeContext";
import { useDispatch, useSelector } from "react-redux";
import { store } from "../../redux/store";
import { increment, incrementAsync } from "../../redux/counterSlice";

function Menu() {

    let { mode, setMode } = useContext(ThemeContext)

    let { setAuth, user } = useContext(AuthContext)

    const u = useMe();

    const counter = useSelector((store) => store.counter);
    const dispatch = useDispatch();
    console.log(counter)

    let handleMode = () => {
        setMode(mode === 'light' ? 'dark' : 'light')
        //   dispatch(increment())
        //  dispatch(increment(10));
        dispatch(incrementAsync())
    }

    let handleLogout = () => {
        setAuth(false)
        localStorage.removeItem("token")
    }
    return (
        <ul>
            {/* <li>Xin chao {user.name}</li> */}
            <li>Xin chao {u.name}</li>
            <li>
                <button onClick={handleMode}>{mode}</button>
            </li>
            <li>
                <Link to="/">Home</Link>
            </li>
            <li>
                <Link to="/categories">Category</Link>
            </li>
            <li>
                <Link to="/user">User</Link>
            </li>
            <li>
                <Link to="/login">Login Page</Link>
            </li>
            <li>
                <Link to="/test">Test Page</Link>
            </li>
            <li>
                <Link to="/posts">Posts</Link>
            </li>
            {/* <li>
                <button onClick={()=> setAuth(false)}>Logout</button>
            </li> */}

            <li>
                <button onClick={handleLogout}>Logout</button>
            </li>
        </ul>
    )
}
export default Menu