
import { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { deletePostAPI, searchPostAPI } from "../../service/post.service";

function ListPost() {
  let navigate = useNavigate()

  let [posts, setPosts] = useState([])
  let [search, setSearch] = useState({
    "start": 0,
    "length": 5,
    "search": {
      "value": ""
    }
  })
  let [totalPage, setTotalPage] = useState(0)

  useEffect(() => {
    fetchData()
  }, [search])

  const fetchData = async () => {
    try {
      let body = await searchPostAPI(search) //lay then tra ve

      console.log(body.data) //thuoc tinh data, xem postman
      setPosts(body.data)
      //tinh tong so trang
      let totalPage = Math.ceil(body.recordsFiltered / search.length)
      setTotalPage(totalPage)
    } catch (e) {
      console.log(e)
    }
  }

  const handleSearch = (e) => {
    setSearch({
      ...search,
      "search": {
        "value": e.target.value
      }
    })
  }

  const handleNext = () => {
    setSearch({
      ...search,
      start: search.start + search.length
    })
  }

  const handlePrev = () => {
    setSearch({
      ...search,
      start: search.start > 0 ? search.start - search.length : 0
    })
  }

  const handleChangeSize = (e) => {
    const size = e.target.value
    setSearch({
      ...search,
      length: parseInt(size)
    })
  }

  const handlePage = (index) => {
    setSearch({
      ...search,
      start: index * search.length
    })
  }

  const handleDelete = async (id) => {
    try {
      await deletePostAPI(id)
      fetchData()
    } catch (e) {

    }
  }

  return <div>
    <Link to="/post/create">Create post</Link>
    <div>
      <input onChange={handleSearch} />
    </div>
    <table border={1}>
      <thead>
        <tr>
          <th>ID</th>
          <th>Title</th>
          <th>Description</th>
          <th>Image</th>
          <th>Category</th>
          <th>Created At</th>
          <th>Created By</th>
          <th>Actions</th>
        </tr>
      </thead>
      <tbody>
        {posts.map(c => {
          return (
            <tr key={c.id}>
              <td>{c.id}</td>
              <td>{c.title}</td>
              <td>{c.description}</td>
              <td>
                {c.images?.map(image => (
                  <img src={`https://jmaster.io/jmaster-reactjs/image/${image}`}
                    alt="" width={100} key={image} />
                ))}
              </td>
              <td>{c.categoryName}</td>
              <td>{c.createdDate}</td>
              <td>{c.createBy}</td>
              <td><button onClick={() => handleDelete(c.id)}>Delete</button>
                | <button onClick={() => navigate(`/post/update/${c.id}`)}>Edit</button></td>
            </tr>
          )
        })}
      </tbody>
    </table>
    <select onChange={handleChangeSize}>
      <option value="5">5</option>
      <option value="10">10</option>
      <option value="20">20</option>
    </select>

    <button onClick={handlePrev}>Prev</button>
    {
      Array.apply(null, { length: totalPage }).map((_, index) => {
        return (
          <button key={index} onClick={() => handlePage(index)}>{index}</button>
        )
      })
    }
    <button onClick={handleNext}>Next</button>

    <p>Tong so trang: {totalPage}</p>
  </div>
}

export default ListPost